export function disabledColorByTheme(theme: ThemeOptions) {
  return theme.mode === "dark"
    ? color(theme?.colors?.disabled).lighten(1.2).string()
    : color(theme?.colors?.disabled).darken(0.3).string();
}

const LIGHT_ELEMENT_COLORS = {
  Fire: "#c30",
  Earth: "#740",
  Water: "#08f",
  Air: "#0b4",
  Nature: "#5b0",
  Lightning: "#80f",
  Darkness: "#444",
};

const DARK_ELEMENT_COLORS = {
  Fire: "#820",
  Earth: "#630",
  Water: "#048",
  Air: "#063",
  Nature: "#360",
  Lightning: "#529",
  Darkness: "#444",
};

export function showImportFromFile(): boolean {
  return Platform.select({
    ios: true,
    android: true,
    default: false,
  });
}

export function showExportToFile(): boolean {
  return Platform.select({
    ios: true,
    android: true,
    web: true,
    default: false,
  });
}

export function elementColorByTheme(
  themeMode: ThemeMode,
  element: SpellElement,
) {
  return themeMode === "dark"
    ? DARK_ELEMENT_COLORS[element]
    : LIGHT_ELEMENT_COLORS[element];
}

export function getPrimaryButtonTextColor(
  theme: ThemeOptions,
  disabled: boolean,
): string {
  if (disabled) {
    return disabledColorByTheme(theme);
  }
  return "white";
}

export function DescriptionViewer(props: any) {
  const theme = useTheme();
  return (
    <Markdown style={[{ color: theme.theme.colors.grey1 }, props.style || {}]}>
      {props.children}
    </Markdown>
  );
}

export function EffectViewer(props: any) {
  const theme = useTheme();
  return (
    <Markdown
      style={[
        styles.effectText,
        { color: theme.theme.colors.grey1 },
        props.style || {},
      ]}
    >
      {props.children}
    </Markdown>
  );
}

