import moment from "moment";
import React, { useContext, useRef } from "react";
import { StyleSheet, View } from "react-native";
import {
  getNewGamePlusBuffLevel,
  getNewGamePlusCount,
  getNewGamePlusDifficultyLevel,
} from "../backend/NewGamePlus";
import { PlayerContext } from "../backend/PlayerContext";
import { Challenges } from "../backend/challenges/Challenges";
import { getEnding, getEndingData } from "../backend/endings/Endings";
import { Storylines } from "../backend/storylines/Storylines";
import { formatNumber, formatTimeLong } from "../utils/FormattingUtils";
import Divider from "./utility/Divider";
import { Markdown } from "./utility/Markdown";
import { Text } from "./utility/Text";

type RowProps = {
  caption: string;
  value: string;
};

function Row(props: RowProps) {
  return (
    <View style={styles.row}>
      <View style={styles.rowCaption}>
        <Text numberOfLines={2}>{props.caption}</Text>
      </View>
      <Text style={styles.valueText}>{props.value}</Text>
    </View>
  );
}

export default function Ending() {
  const playerContext = useContext(PlayerContext);
  const playerContextRef = useRef(playerContext);
  playerContextRef.current = playerContext;

  const endingData = getEndingData(playerContext);
  if (endingData == null) {
    return <View />;
  }

  const ending = getEnding(endingData.endingId);

  const totalDuration = moment.duration(endingData.totalTimePlayed, "seconds");
  const thisWorldDuration = moment.duration(
    endingData.totalTimePlayedThisWorld,
    "seconds",
  );
  const thisRunDuration = moment.duration(
    endingData.totalTimePlayedThisRun,
    "seconds",
  );
  const totalActiveDuration =
    endingData.totalActiveTimePlayed != null
      ? moment.duration(endingData.totalActiveTimePlayed, "seconds")
      : null;
  const totalActiveDurationThisWorld =
    endingData.totalActivePlayedThisWorld != null
      ? moment.duration(endingData.totalActivePlayedThisWorld, "seconds")
      : null;
  const totalRealDuration =
    endingData.totalRealWorldTimePlayed != null
      ? moment.duration(endingData.totalRealWorldTimePlayed, "seconds")
      : null;
  const totalRealDurationThisWorld =
    endingData.totalRealWorldTimePlayedThisWorld != null
      ? moment.duration(endingData.totalRealWorldTimePlayedThisWorld, "seconds")
      : null;

  const newGameCount = getNewGamePlusCount(playerContext);
  const worldBoonLevel = getNewGamePlusBuffLevel(playerContext);
  const worldDifficultyLevel = getNewGamePlusDifficultyLevel(playerContext);

  const allStorylines = Storylines.getAll();

  return (
    <View style={styles.container}>
      <Text h1>{ending?.title}</Text>
      <View style={styles.explanation}>
        <Markdown style={styles.explanationText}>
          {ending?.description}
        </Markdown>
      </View>
      <View style={styles.dividerContainer}>
        <Divider />
      </View>
      <View style={styles.congratsContainer}>
        <Text h1>Congratulations!</Text>
        <Text h2>You have cleared Magic Research!</Text>
      </View>
      <View>
        <Row
          caption="Primary School and Element:"
          value={`${endingData.primarySchool} - ${endingData.primaryElement}`}
        />
        {endingData.challengeId && (
          <Row
            caption="Challenge:"
            value={Challenges.getById(endingData.challengeId).getName()}
          />
        )}
        {newGameCount > 0 && (
          <Row
            caption="World Modifiers:"
            value={`Boon ${formatNumber(worldBoonLevel)} / Diff ${formatNumber(
              worldDifficultyLevel,
            )}`}
          />
        )}
        <Row
          caption="Total In-Game Time Played:"
          value={formatTimeLong(totalDuration)}
        />
        {newGameCount > 0 && (
          <Row
            caption="In-Game Time Played (This World):"
            value={formatTimeLong(thisWorldDuration)}
          />
        )}
        <Row
          caption="In-Game Time Played (This Run):"
          value={formatTimeLong(thisRunDuration)}
        />
        {totalActiveDuration != null && (
          <Row
            caption="Total Real World Time Played:"
            value={formatTimeLong(totalActiveDuration)}
          />
        )}
        {totalActiveDurationThisWorld != null && newGameCount > 0 && (
          <Row
            caption="Real World Time Played (This World):"
            value={formatTimeLong(totalActiveDurationThisWorld)}
          />
        )}
        {totalRealDuration != null && (
          <Row
            caption="Total Time Since Starting Game:"
            value={formatTimeLong(totalRealDuration)}
          />
        )}
        {totalRealDurationThisWorld != null && newGameCount > 0 && (
          <Row
            caption="Total Time Since Starting This World:"
            value={formatTimeLong(totalRealDurationThisWorld)}
          />
        )}
        {newGameCount > 0 && (
          <Row caption="Worlds Created:" value={newGameCount.toString()} />
        )}
        <Row
          caption="Total Retirements:"
          value={playerContext.global.totalTimesResetted.toString()}
        />
        <Row
          caption="Maximum Primary Level Obtained:"
          value={endingData.mpl.toString()}
        />
        <Row
          caption="Primary School Level on Game Beaten:"
          value={endingData.primarySchoolLevelOnEnding.toString()}
        />
        <Row
          caption="Global Research Multiplier:"
          value={`${formatNumber(endingData.researchMultiplier, {
            showDecimals: true,
          })}x`}
        />
        <Row
          caption="Global Production Multiplier:"
          value={`${formatNumber(endingData.productionMultiplier, {
            showDecimals: true,
          })}x`}
        />
        <Row
          caption="Total Completed Storylines:"
          value={`${endingData.completedStorylines} / ${allStorylines.length}`}
        />
        <Row
          caption="Storyline Completion Percentage:"
          value={
            formatNumber(
              (100 * endingData.completedStorylines) / allStorylines.length,
            ) + "%"
          }
        />
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  explanation: {
    marginTop: 16,
    flex: 1,
    alignItems: "stretch",
    width: "100%",
  },
  explanationText: {
    width: "100%",
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: 4,
  },
  valueText: {
    minWidth: 100,
    textAlign: "right",
    fontWeight: "bold",
    marginLeft: 8,
  },
  dividerContainer: {
    marginTop: 32,
    marginBottom: 24,
  },
  congratsContainer: {
    alignItems: "center",
    marginBottom: 16,
  },
  rowCaption: {
    flex: 1,
  },
});
