import {
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "../calculation/Calculation";
import { TransformationTags } from "../calculation/TransformationTags";
import { addContextListener } from "../PlayerContextListeners";
import {
  getTemporaryEffectEndTime,
  grantTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectListener,
} from "../timetick/TemporaryEffects";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { AttackTarget } from "./AttackTarget";
import { CombatStat } from "./CombatStats";
import { getExplorationStatus, resetEnemyNextAction } from "./Exploration";
import { ExplorationStatus } from "./ExplorationStatus";

export function loadStunning() {
  // While stunned, enemies and players both have a giant attack delay
  registerTransformation(
    [[AttackTarget.Player, CombatStat.AttackDelay]],
    "PlayerStunAttackDelay",
    TransformationType.Addition,
    (state) => (hasTemporaryEffect(state, "PlayerStun") ? +Infinity : 0),
  );
  registerTransformation(
    [[AttackTarget.Enemy, CombatStat.AttackDelay]],
    "EnemyStunAttackDelay",
    TransformationType.Addition,
    (state) => (hasTemporaryEffect(state, "EnemyStun") ? +Infinity : 0),
  );

  registerTemporaryEffectListener(
    "PlayerStunning",
    (newState, temporaryEffect) => {
      if (temporaryEffect.id === "PlayerStun") {
        // Player's been stunned, but does it have temporary immunity?
        if (hasTemporaryEffect(newState, "PlayerStunPrevention")) {
          temporaryEffect.endTime = getTemporaryEffectEndTime(
            newState,
            "PlayerStun",
          );
          return temporaryEffect;
        }
      }
      return temporaryEffect;
    },
  );

  addContextListener(
    "PlayerStunning",
    (newState) => {
      if (hasTemporaryEffect(newState, "PlayerStun")) {
        const stunDuration =
          getTemporaryEffectEndTime(newState, "PlayerStun") -
          getSecondsPlayed(newState);

        // Player's stun has been set. Set attack timer start time to estimated stun end
        // so that on heal we don't immediately attack. Also give stun protection.
        return (state) => {
          state.exploration.playerActionProgress = 0.0;
          state = grantTemporaryEffect(
            "PlayerStunPrevention",
            "Stun Protection",
            applyTransformations(
              [
                AttackTarget.Player,
                TransformationTags.PostStunProtectionDuration,
              ],
              newState,
              6.0,
            ) + stunDuration,
            { mode: "add", combatOnly: true },
          )(state);
          return state;
        };
      }
    },
    (state) => getTemporaryEffectEndTime(state, "PlayerStun"),
  );

  addContextListener(
    "PlayerStunningRecovery",
    (newState) => {
      if (!hasTemporaryEffect(newState, "PlayerStun")) {
        // Player's stun has run out, so restart the attack timer
        return (state) => {
          state.exploration.playerActionProgress = 0.0;
          return state;
        };
      }
    },
    (state) => hasTemporaryEffect(state, "PlayerStun"),
  );

  registerTemporaryEffectListener(
    "EnemyStunning",
    (newState, temporaryEffect) => {
      if (temporaryEffect.id === "EnemyStun") {
        // Enemy's been stunned, but does it have temporary immunity?
        if (hasTemporaryEffect(newState, "EnemyStunPrevention")) {
          temporaryEffect.endTime = getTemporaryEffectEndTime(
            newState,
            "EnemyStun",
          );
          return temporaryEffect;
        }
      }
      return temporaryEffect;
    },
  );

  addContextListener(
    "EnemyStunning",
    (newState) => {
      if (hasTemporaryEffect(newState, "EnemyStun")) {
        const stunDuration =
          getTemporaryEffectEndTime(newState, "EnemyStun") -
          getSecondsPlayed(newState);

        // Enemy's stun has been set. Set attack timer start time to estimated stun end
        // so that on heal we don't immediately attack. Also give stun protection.
        return (state) => {
          state.exploration.enemyActionProgress = 0.0;
          if (getExplorationStatus(state) === ExplorationStatus.Combat) {
            state = grantTemporaryEffect(
              "EnemyStunPrevention",
              "Stun Protection",
              applyTransformations(
                [
                  AttackTarget.Enemy,
                  TransformationTags.PostStunProtectionDuration,
                ],
                newState,
                6.0,
              ) + stunDuration,
              { mode: "add", combatOnly: true, isEnemyTemporaryEffect: true },
            )(state);
          }
          return state;
        };
      }
    },
    (state) => getTemporaryEffectEndTime(state, "EnemyStun"),
  );

  addContextListener(
    "EnemyStunningRecovery",
    (newState) => {
      if (!hasTemporaryEffect(newState, "EnemyStun")) {
        // Enemy's stun has run out, so restart the attack timer
        return (state) => {
          resetEnemyNextAction();
          state.exploration.enemyActionProgress = 0.0;
          return state;
        };
      }
    },
    (state) => hasTemporaryEffect(state, "EnemyStun"),
  );
}
